html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

.cabeza {
  width: 20%;
  height: 10%;
}
.cabeza .titulo {
  color: white;
  font-size: 30px;
  font-family: "Times New Roman", Times, serif;
}

body {
  background-color: #fc0233;
}
body .nav {
  width: 20%;
  min-height: 70%;
  height: 70%;
}
body .nav .lista {
  color: black;
  font-size: 20px;
  font-family: "Times New Roman", Times, serif;
  list-style: none;
}
body .nav .lista li {
  margin: 3px;
}

footer {
  width: 10%;
  height: 5%;
}
footer ul {
  color: hsla(0, 0%, 100%, 0.2);
  font-size: 10px;
  font-family: "Times New Roman", Times, serif;
}
footer ul li {
  margin: 2px;
}